import React, { useState, useEffect } from "react";
import { Box, TextField, Button, Snackbar } from "@mui/material";
import { makeStyles } from "@mui/styles";
import store from "../store";
import { signin_input_change, signin_user } from "../actions/authActions";

const style = {
  marginLeft: "20px",
  marginTop: "20px",
};

const useStyles = makeStyles((theme) => ({
  dialogAction: {
    display: "flex",
    justifyContent: "flex-end",
    [theme.breakpoints.down("sm")]: {
      justifyContent: "center",
    },
  },
}));

export default function Signin({ handleClose, authSuccess }) {
  const initialValues = {
    username: "",
    password: "",
  };
  const errorsSchema = {
    username: [],
    password: [],
    general: [],
  };
  const initAuth = {
    username: "",
    email: "",
    token: "",
  };

  const [auth, setAuth] = useState(initAuth);
  const [values, setValues] = useState(initialValues);
  const [errors, setErrors] = useState(errorsSchema);
  const [openAlert, setOpenAlert] = React.useState(errors.general.length > 0);
  const [alertMessage, setAlertMessage] = React.useState("");

  useEffect(() => {
    setAuth(store.getState().auth.user);
    setValues(store.getState().auth.signin);
    setErrors(store.getState().auth.signin_errors);
  }, []);

  const classes = useStyles();

  const handleInputChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setValues({
      ...values,
      [name]: value,
    });
    store.dispatch(signin_input_change({ name, value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    store.dispatch(signin_user(values)).then(function() {
      setErrors(store.getState().auth.signin_errors);
      setAuth(store.getState().auth.user);
      if (store.getState().auth.user.token !== "") {
        handleClose();
        authSuccess(true);
      }
      setAlertMessage(errors.general.length > 0 ? errors.general[0] : "");
    })
  };

  const closeAlert = () => {
    setOpenAlert(false);
  }

  return (
    <Box
      component='form'
      onSubmit={handleSubmit}
      sm={{
        m: "auto",
        display: "flex",
        flexDirection: "column",
        width: "100%",
      }}>
      <TextField
        type='text'
        name='username'
        id='username-login'
        label='Username'
        variant='outlined'
        fullWidth={true}
        margin='normal'
        autoComplete='off'
        autoFocus={true}
        value={values.username}
        onChange={handleInputChange}
        error={errors.username.length > 0}
        helperText={errors.username.map((err) => (
          <li className='auth_error' key={err}>
            {err}
          </li>
        ))}
      />
      <TextField
        type='password'
        name='password'
        id='password-login'
        label='Password'
        variant='outlined'
        fullWidth={true}
        margin='normal'
        value={values.password}
        onChange={handleInputChange}
        error={errors.password.length > 0}
        helperText={errors.password.map((err) => (
          <li className='auth_error' key={err}>
            {err}
          </li>
        ))}
      />
      <div className={classes.dialogAction}>
        <Button
          type='button'
          style={style}
          variant='outlined'
          onClick={handleClose}>
          Cancel
        </Button>
        <Button type='submit' style={style} variant='contained'>
          Signin
        </Button>
      </div>
      <Snackbar
          anchorOrigin={{vertical: 'bottom', horizontal: 'center'}}
          open={errors.general.length > 0 || openAlert ? true : false}
          onClose={closeAlert}
          message={alertMessage}
          key={{vertical: 'center', horizontal: 'bottom'}}
        />
    </Box>
  );
}
