import React, {useState, useEffect} from 'react'
import {Box, TextField, Button} from '@mui/material'
import {makeStyles} from '@mui/styles'
import {signup_input_change, signup_user} from '../actions/authActions';
import store from '../store';

const style = {
  marginLeft: '20px',
  marginTop: '20px'
}

const useStyles = makeStyles((theme) => ({
  dialogAction: {
    display: 'flex',
    justifyContent: 'flex-end',
    [theme.breakpoints.down('sm')]: {
      justifyContent: 'center'
    }
  }
})
)

const Signup = ({handleClose, authSuccess}) => {
  const initialValues = {
    username: '',
    email: '',
    password: ''
  }
  const errorsSchema = {
    username: [],
    email: [],
    password: [],
    general: []
  }
  const initAuth = {
    username: '',
    email: '',
    token: '',
    role: '',
    profile_pic: ''
  }
  const [auth, setAuth] = useState(initAuth);
  const [values, setValues] = useState(initialValues);
  const [errors, setErrors] = useState(errorsSchema);

  useEffect(() => {
    setValues(store.getState().auth.signup);
    setErrors(store.getState().auth.signup_errors);
    setAuth(store.getState().auth.user);
  },[]);


  const classes = useStyles();
  const handleInputChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setValues({
      ...values,
      [name]:value
    })
    store.dispatch(signup_input_change({name, value}));
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    store.dispatch(signup_user(values)).then(function() {
      setErrors(store.getState().auth.signup_errors);
      setAuth(store.getState().auth.user);
      if(store.getState().auth.user.token !== "") {
        handleClose();
        authSuccess(true);
      }
    });
  }
  if(errors.general.length > 0) {
    console.log(errors.general);
  }
  return (
        <Box component="form" onSubmit={handleSubmit} sm={{m: 'auto', display: 'flex', flexDirection: 'column', width: '100%'}}>
            <TextField 
              type="text" 
              name="username" 
              id="username" 
              label="Username" 
              variant="outlined" 
              fullWidth={true} 
              margin="normal"
              autoComplete='off'
              autoFocus={true}
              value={values.username}
              onChange={handleInputChange}
              error={errors.username.length > 0}
              helperText={
                errors.username.map((err) => <li className='auth_error' key={err}>{err}</li>)
              }
            />
            <TextField 
              type="text" 
              name="email" 
              id="email" 
              label="Email address" 
              variant="outlined" 
              fullWidth={true} 
              margin="normal"
              autoComplete='off'
              value={values.email}
              onChange={handleInputChange}
              error={errors.email.length > 0}
              helperText={
                errors.email.map((err) => <li className='auth_error' key={err}>{err}</li>)
              }
            />
            <TextField 
              type="password" 
              name="password" 
              id="password" 
              label="Password" 
              variant="outlined" 
              fullWidth={true} 
              margin="normal" 
              value={values.password}
              onChange={handleInputChange}
              error={errors.password.length > 0}
              helperText={
                errors.password.map((err) => <li className='auth_error' key={err}>{err}</li>)
              }
            />
            <Box component="div" className={classes.dialogAction}>
              <Button style={style} variant="outlined" type="button" onClick={handleClose}>Cancel</Button>
              <Button style={style} variant="contained" type="submit">Signup</Button>
            </Box>
        </Box>
  )
}


export default Signup
