import axios from 'axios';
import {HOST,AUTH_FAIL, GET_ADDRESS, REMOVE_ADDRESS, SET_ADDRESS} from './types';

export const get_address = () => dispatch => {
    return new Promise((resolve, reject) => {
        dispatch({
            type: GET_ADDRESS
        })
        resolve();
    })
}

export const set_address = ({address, balance}) => dispatch => {
    return new Promise((resolve, reject) => {
        axios({
            headers: {
                "Content-Type": "application/json",
                "Authorization": localStorage.getItem('_token') === null ? '' : localStorage.getItem('_token')
            },
            method: 'post',
            url: `${HOST}/wallet`,
            data: JSON.stringify({address:address, balance: balance})
        }).then(function(response) {
            if(response.status === 200 || response.status === 201) {
                dispatch({
                    type: SET_ADDRESS,
                    payload: {
                        address
                    }
                })
            }
        }).catch(function(error) {
            if(error.response.status === 403 && error.response.data.success === false) {
                dispatch({
                    type: AUTH_FAIL
                })
            }
        })
        resolve();
    })
}

export const remove_address = () => dispatch => {
    dispatch({
        type: REMOVE_ADDRESS
    })
}