import React, { useState, useRef } from "react";
import { makeStyles } from "@mui/styles";
import {
  Paper,
  Box,
  FormGroup,
  FormControl,
  Typography,
  Button,
  TextField,
  Snackbar,
  Divider
} from "@mui/material";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";
import { uploadNft } from "../actions/nftActions";
import store from "../store";
import {Navigate} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "20px",
    marginLeft: "260px",
    marginTop: "20px",
    marginRight: "20px",
    [theme.breakpoints.down("sm")]: {
      marginLeft: "10px",
      marginTop: "10px",
      marginRight: "10px",
    },
  },
  fileUpload: {
    maxWidth: "300px",
    maxHeight: "300px",
    width: "250px",
    height: "200px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: "14px",
    border: "2px dashed #05595B",
    borderRadius: "10px",
    padding: "10px",
    marginTop: "10px",
    cursor: "pointer",
    background: "#ffffff",
    "&:hover": {
      border: "2px solid #E9A6A6",
      background: "#F7E2E2",
      color: "#fff",
    },
    [theme.breakpoints.down("sm")]: {
      maxWidth: "100%",
      width: "100%",
      padding: "0px 10px",
    },
  },
  fileInput: {
    display: "none",
  },
  fileUploadIcon: {
    display: "block",
    fontSize: "60px",
    color: "#05595B",
  },
  submitButton: {
    marginTop: "20px",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
  uploadImagePreview: {
    width: "250px",
    height: "200px",
    borderRadius: "10px",
    [theme.breakpoints.down("sm")]: {
      maxWidth: "100%",
      width: "100%",
    },
  },
  item_name: {
    marginTop: "30px",
    width: "400px",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
}));

const CreateNFT = ({authorized}) => {
  const errorSchema = {
    nftImage: [],
    item_name: [],
    general: [],
  };

  const inputFileRef = useRef(null);
  const [openAlert, setOpenAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [errors, setErrors] = useState(errorSchema);
  const [uploadedFile, setUploadedFile] = useState(null);
  const [itemName, setItemName] = useState("");
  const [filename, setFilename] = useState("");
  const classes = useStyles();

  const handleSubmit = (event) => {
    event.preventDefault();
    if (uploadedFile && itemName.trim() !== "") {
      const formData = new FormData();
      formData.append("ImageFile", uploadedFile, uploadedFile.name);
      formData.append("filename", uploadedFile.name);
      formData.append("item_name", itemName);
      store
        .dispatch(uploadNft(formData))
        .then(function (response) {
          setItemName("");
          setUploadedFile(null);
          setAlertMessage("Nft created successfully.");
          setOpenAlert(true);
        })
        .catch(function (error) {
          setErrors(error);
          if(errors.general.length > 0) {
              setAlertMessage(errors.general[0]);
              setOpenAlert(true);
          }
        });
    } else {
        setErrors({
            ...errors,
            nftImage: (function() {
                if(!uploadedFile) {
                    return ["Image file is required."];
                }
            })(),
            item_name: (function() {
                if(itemName.trim() === '') {
                    return ["Item name is required."];
                }
            })()
        })
        
      setAlertMessage('All input field are required.');
      setOpenAlert(true);
    }
  };

  const fileInputChange = (event) => {
    if (event.target.files[0]) {
        setErrors({
            ...errors,
            nftImage: []
        })
      var reader = new FileReader();
      reader.onload = function (e) {
        setFilename(e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      setUploadedFile(event.target.files[0]);
    }
  };

  const InputChange = (event) => {
      setErrors({
          ...errors,
          item_name: ''
      })
    setItemName(event.target.value);
  };

  const fileUploadClick = () => {
    inputFileRef.current.click();
  };

  const closeAlert = () => {
    setOpenAlert(false);
  }
  return (
    authorized 
      ? <Paper className={classes.root} elevation={2} style={{border: '1px solid #FAD9E6'}}>
      <Box
        component='form'
        onSubmit={handleSubmit}
        sm={{
          m: "auto",
          display: "flex",
          flexDirection: "column",
          width: "100%",
        }}>
        <Typography
          sx={{ flex: "1 1 100%" }}
          variant='h6'
          id='tableTitle'
          component='div'
        >
          Create NFT
        </Typography>
        <Divider />
        <FormGroup style={{marginTop: "20px"}}>
          <FormControl>
            <Typography>File Upload</Typography>
            <span style={{fontSize: '12px'}}>Supported (jpeg, jpg, png)</span>
            <input
              type='file'
              name='nftImage'
              ref={inputFileRef}
              className={classes.fileInput}
              id='fileInput'
              onChange={fileInputChange}
            />
            <Box
            style={{border: errors.nftImage.length > 0 ? "2px solid red" : "2px dashed #05595B"}}
              component='div'
              role='button'
              className={classes.fileUpload}
              id='FileUpload'
              onClick={fileUploadClick}>
              {uploadedFile ? (
                <img src={filename} className={classes.uploadImagePreview} />
              ) : (
                <AddPhotoAlternateIcon className={classes.fileUploadIcon} />
              )}
              {errors.nftImage.length > 0 ? <span>{errors.nftImage[0]}</span> : ''}
            </Box>
            <TextField
              name='item_name'
              className={classes.item_name}
              variant='outlined'
              label='Item name'
              onChange={InputChange}
              margin='normal'
              value={itemName}
              error={errors.item_name.length > 0}
              helperText={errors.item_name[0]}
            />
          </FormControl>
        </FormGroup>
        <Button
          className={classes.submitButton}
          variant='contained'
          type='submit'>
          Create NFT
        </Button>
      </Box>
      <Snackbar
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        open={openAlert}
        onClose={closeAlert}
        message={alertMessage}
        key={{ vertical: "center", horizontal: "bottom" }}
      />
    </Paper> 
    : <Navigate to="/" replace />
  );
};

export default CreateNFT;
