import {LOAD_NFTS, LOAD_NFTS_FAIL, UPDATE_NFT_SUCCESS, UPDATE_NFT_FAIL} from '../actions/types';

const initialState = {
    nftForm: {
        file: null,
        item_name: ''
    },
    errors: {
        nftImage: [],
        item_name: [],
        general: []
    },
    nfts: []
}

export default function NFTReducer(state = initialState, action) {
    switch(action.type) {
        case LOAD_NFTS:
            return {
                ...state,
                nfts: action.payload.data
            }
        case UPDATE_NFT_FAIL:
            return {
                ...state,
                errors: action.payload
            }
        case UPDATE_NFT_SUCCESS:
            return {
                errors: {
                    nftImage: [],
                    item_name: [],
                    general: []
                },
                nfts: action.payload.data
            }
        case LOAD_NFTS_FAIL: {
            return {
                ...state,
                nfts: []
            }
        }
        default:
            return state
    }
}