import React, { useState, useRef } from "react";
import { makeStyles } from "@mui/styles";
import {
  Paper,
  Box,
  FormGroup,
  FormControl,
  Typography,
  Button,
  TextField,
  Snackbar,
  Divider
} from "@mui/material";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";
import {useLocation, Navigate, useNavigate} from 'react-router-dom';
import store from '../store';
import {updateNfts} from '../actions/nftActions'


const useStyles = makeStyles((theme) => ({
  root: {
    padding: "10px",
    marginLeft: "260px",
    marginTop: "20px",
    marginRight: "20px",
    [theme.breakpoints.down("sm")]: {
      marginLeft: "10px",
      marginTop: "10px",
      marginRight: "10px",
    },
  },
  fileUpload: {
    maxWidth: "300px",
    maxHeight: "300px",
    width: "250px",
    height: "200px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: "14px",
    border: "2px dashed #05595B",
    borderRadius: "10px",
    padding: "10px",
    marginTop: "10px",
    cursor: "pointer",
    background: "#ffffff",
    "&:hover": {
      border: "2px solid #E9A6A6",
      background: "#F7E2E2",
      color: "#fff",
    },
    [theme.breakpoints.down("sm")]: {
      maxWidth: "100%",
      width: "100%",
      padding: "0px 10px",
    },
  },
  fileInput: {
    display: "none",
  },
  fileUploadIcon: {
    display: "block",
    fontSize: "60px",
    color: "#05595B",
  },
  submitButton: {
    marginTop: "20px",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
  uploadImagePreview: {
    width: "250px",
    height: "200px",
    borderRadius: "10px",
    [theme.breakpoints.down("sm")]: {
      maxWidth: "100%",
      width: "100%",
    },
  },
  item_name: {
    marginTop: "30px",
    width: "400px",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
}));

const Updatenft = ({authorized}) => {
  
  const navigate = useNavigate();
  const errorSchema = {
    nftImage: [],
    item_name: [],
    general: [],
  };

  const nft = {
    nftImage: "",
    item_name: ""
  }

  const inputFileRef = useRef(null);
  const [updateNft, setUpdateNft] = useState(nft);
  const [notUpdated, setNotUpdated] = useState(true);
  const [openAlert, setOpenAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [errors, setErrors] = useState(errorSchema);
  const [uploadedFile, setUploadedFile] = useState(null);
  const [itemName, setItemName] = useState("");
  const [filename, setFilename] = useState("");
  const classes = useStyles();
  const location = useLocation();

  React.useEffect(() => {
    setUpdateNft({
      ...updateNft,
      nftImage: location.state.nftImage
    });
    setItemName(location.state.itemName);
  },[])

  const handleSubmit = (event) => {
    event.preventDefault();
    if(notUpdated) {
      setAlertMessage("You haven't changed anything.");
      setOpenAlert(true);
    } else {
      if (uploadedFile || itemName.trim() !== "") {
        const formData = new FormData();
        formData.append("id", location.state.id);
        if(uploadedFile !== null) {
          formData.append("ImageFile", uploadedFile, uploadedFile.name);
          formData.append("filename", uploadedFile.name);
        }
        if(itemName !== '') {
          formData.append("item_name", itemName);
        }
        // TODO::dispatch updateNft action
        store.dispatch(updateNfts(formData)).then(function({nft, nfts}) {
          console.log(nfts);
          setAlertMessage("Nft updated successfully.");
          setOpenAlert(true);
          navigate('/list-nft', {state: {nfts}});
        }).catch(function(error) {
          console.log(error);
        });
      } else {
          setErrors({
            ...errors,
            nftImage: (function() {
                if(!uploadedFile && location.state.nftImage === "") {
                  return ["Image file is required."];
                } else {
                  return [];
                }
            })(),
            item_name: (function() {
                if(itemName.trim() === '') {
                  return ["Item name is required."];
                } else {
                  return [];
                }
            })()
          })
          
        setAlertMessage('All input field are required.');
        setOpenAlert(true);
      }
    }
  };

  const fileInputChange = (event) => {
    if (event.target.files[0]) {
        setErrors({
            ...errors,
            nftImage: []
        })
      var reader = new FileReader();
      reader.onload = function (e) {
        setFilename(e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      setUploadedFile(event.target.files[0]);
      setUpdateNft({
        ...updateNft,
        nftImage: ''
      });
      setNotUpdated(false);
    }
  };

  const InputChange = (event) => {
    setUpdateNft({
      ...updateNft,
      item_name: ''
    });
      setErrors({
          ...errors,
          item_name: []
      })
    setItemName(event.target.value);
    setNotUpdated(false);
  };

  const fileUploadClick = () => {
    inputFileRef.current.click();
  };

  const closeAlert = () => {
    setOpenAlert(false);
  }
  return (
    authorized 
      ? <Paper className={classes.root} elevation={2} style={{border: '1px solid #FAD9E6'}}>
      <Box
        component='form'
        onSubmit={handleSubmit}
        sm={{
          m: "auto",
          display: "flex",
          flexDirection: "column",
          width: "100%",
        }}>
          <Typography
          sx={{ flex: "1 1 100%" }}
          variant='h6'
          id='tableTitle'
          component='div'
        >
          Update NFT
        </Typography>
        <Divider />
        <FormGroup style={{marginTop: "20px"}}>
          <FormControl>
            <Typography>File Upload</Typography>
            <span style={{fontSize: '12px'}}>Supported (jpeg, jpg, png)</span>
            <input
              type='file'
              name='nftImage'
              ref={inputFileRef}
              className={classes.fileInput}
              id='fileInput'
              onChange={fileInputChange}
            />
            <Box
            style={{border: errors.nftImage.length > 0 ? "2px solid red" : "2px dashed #05595B"}}
              component='div'
              role='button'
              className={classes.fileUpload}
              id='FileUpload'
              onClick={fileUploadClick}>
              {uploadedFile || updateNft.nftImage !== "" ? (
                <img src={updateNft.nftImage === "" ? filename : updateNft.nftImage} className={classes.uploadImagePreview} />
              ) : (
                <AddPhotoAlternateIcon className={classes.fileUploadIcon} />
              )}
            </Box>
            <span style={{color:'red', marginTop: '7px'}}>{errors.nftImage.length > 0 ? <span>{errors.nftImage[0]}</span> : ''}</span>
            <TextField
              name='item_name'
              className={classes.item_name}
              variant='outlined'
              label='Item name'
              onChange={InputChange}
              margin='normal'
              value={itemName}
              error={errors.item_name.length > 0}
              helperText={errors.item_name[0]}
            />
          </FormControl>
        </FormGroup>
        <Button
          className={classes.submitButton}
          variant='contained'
          type='submit'>
          Update NFT
        </Button>
      </Box>
      <Snackbar
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        open={openAlert}
        onClose={closeAlert}
        message={alertMessage}
        key={{ vertical: "center", horizontal: "bottom" }}
      />
    </Paper> 
    : <Navigate to="/" replace />
  );
}

export default Updatenft;
