import {GET_ADDRESS, SET_ADDRESS, REMOVE_ADDRESS} from '../actions/types';

const initialState = {
    list: [
        {
            title: 'Metamask',
            link: 'metamask',
            icon: '/metamask.png'
        }
    ],
    connected_wallet_address: ''
};

export default function wallet(state = initialState, action) {
    switch(action.type) {
        case GET_ADDRESS:
            return {
                ...state,
            }
        case SET_ADDRESS:
            return {
                ...state,
                'connected_wallet_address': action.payload.address
            }
        case REMOVE_ADDRESS:
            return {
                ...state,
                'connected_wallet_address': ""
            }
        default:
            return state;
    }
}