import React from 'react';
import { makeStyles } from '@mui/styles';
import {Paper} from '@mui/material';

const useStyles = makeStyles(theme => ({
    root: {
        padding: "10px",
        marginTop: "20px",
        marginLeft: "260px",
        marginRight: "20px",
        [theme.breakpoints.down('sm')]: {
            marginLeft: "10px",
            marginRight: "10px",
            marginTop: "10px"
        }
    }
}))

const Home = () => {
    const classes = useStyles();
    return (
        <Paper className={classes.root}>
            Home Page...
        </Paper>
    );
}

export default Home;
