import "./App.css";
import React, { useState, useEffect } from "react";
import { Provider } from "react-redux";
import store from "./store";
import { createTheme, ThemeProvider, Snackbar, Button } from "@mui/material";
import { verify_token } from "./actions/authActions";
import Header from "./components/Header";
import { remove_address, set_address } from "./actions/walletAction";
import { ethers } from "ethers";
import Sidebar from "./components/Sidebar";
import { BrowserRouter } from "react-router-dom";
import Router from "./Router";
import { loadNfts } from "./actions/nftActions";

const theme = createTheme({
  palette: {
    primary: {
      light: "#FD5D5D",
      main: "#890F0D",
      dark: "#180A0A",
    },
    secondary: {
      light: "#FFF7BC",
      main: "#E83A14",
      dark: "#F582A7",
    },
  },
});

const App = () => {
  const [walletAddress, setWalletAddress] = useState(
    store.getState().wallet.connected_wallet_address
  );
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const formatWalletAddress = (address) => {
    return address !== ""
      ? address.substring(0, 5) +
          "..." +
          address.substring(address.length - 4, address.length)
      : "";
  };

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const [authorized, setAuthorized] = useState(false);
  const [openAlert, setOpenAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [mount, setMount] = useState(false);

  useEffect(() => {
    if(!mount) {
      store.dispatch(verify_token()).then(function () {
        if (store.getState().auth.user.token !== "") {
          if (!authorized) setAuthorized(true);
          if (
            typeof window.ethereum !== "undefined" &&
            window.ethereum.isMetaMask
          ) {
            if (store.getState().wallet.connected_wallet_address === "") {
              window.ethereum
              .request({ method: "eth_accounts" })
              .then(function (accounts) {
                if (accounts.length > 0) {
                  window.ethereum
                    .request({
                      method: "eth_getBalance",
                      params: [accounts[0], "latest"],
                    })
                    .then(function (balance) {
                      if (window.ethereum.networkVersion === "4") {
                        store
                          .dispatch(
                            set_address({
                              address: accounts[0],
                              balance: balance,
                            })
                          )
                          .then(function () {
                            setWalletAddress(formatWalletAddress(accounts[0]));
                          });
                      }
                    })
                    .catch(function (err) {
                      console.log(err.message);
                    });
                }
              })
              .catch(function (error) {
                console.log(error.message);
              });
            }
            window.ethereum.on("accountsChanged", (accounts) => {
              if (accounts.length > 0) {
                if (walletAddress !== formatWalletAddress(accounts[0])) {
                  window.ethereum
                  .request({
                    method: "eth_getBalance",
                    params: [accounts[0], "latest"],
                  })
                  .then(function (balance) {
                    if (window.ethereum.networkVersion === "4") {
                      store
                        .dispatch(
                          set_address({
                            address: accounts[0],
                            balance: balance,
                          })
                        )
                        .then(function () {
                          setWalletAddress(formatWalletAddress(accounts[0]));
                        });
                    }
                  })
                  .catch(function (error) {
                    console.log(error);
                  });
                }
              } else {
                if (walletAddress !== "") {
                  store.dispatch(remove_address());
                  setWalletAddress("");
                }
              }
            });
            window.ethereum.on("chainChanged", (network) => {
              if(network !== 0x4) {
                setAlertMessage("Use Only Rinkeby test network for now.");
                setOpenAlert(true);
              }
            })
          }
        }
      });
      setMount(true);
    }
    store.dispatch(loadNfts());
  }, []);

  const authSuccess = (value) => {
    setAuthorized(value);
  };
  const closeAlert = () => {
    setOpenAlert(false);
  }

  const SellArticle = async () => {
    const { ethereum } = window;
    try {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const contract = require("./contracts/MyNFTsToken.json");
      const check = new ethers.Contract(
        "0xDCfbb4fc823AdB4b179E0CfD7402f71B87609286",
        contract.abi,
        signer
      );
      const article = await check.mintToken(
        store.getState().wallet.connected_wallet_address,
        21
      );
      console.log(article);
    } catch (error) {
      if (error.code === 4001) {
        alert(error.message);
      } else {
        console.log(error);
      }
    }
  };

  const GetArticle = async () => {
    const { ethereum } = window;
    try {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const contract = require("./contracts/MyNFTsToken.json");
      const check = new ethers.Contract(
        "0xDCfbb4fc823AdB4b179E0CfD7402f71B87609286",
        contract.abi,
        signer
      );
      const article = await check._baseURI();
      console.log(article);
    } catch (error) {
      if (error.code === 4001) {
        alert(error.message);
      }
    }
  };

  return (
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <Provider store={store}>
          <Header
            authSuccess={authSuccess}
            authorized={authorized}
            walletAddress={walletAddress}
            setWalletAddress={setWalletAddress}
            handleDrawerToggle={handleDrawerToggle}
          />
          <Sidebar
            mobileOpen={mobileOpen}
            handleDrawerToggle={handleDrawerToggle}
            authorized={authorized}
          />
          {/* <Button style={{marginLeft: "250px"}}  onClick={SellArticle} color="secondary" >Sell Article</Button> */}
          <Router authorized={authorized}/>
          <Snackbar
            anchorOrigin={{vertical: 'bottom', horizontal: 'center'}}
            open={openAlert}
            onClose={closeAlert}
            message={alertMessage}
            key={{vertical: 'center', horizontal: 'bottom'}}
          />
          {/* <Button variant='contained' color="secondary" onClick={SellArticle}>Sell Article</Button>
        <Button variant='contained' color="primary" onClick={GetArticle}>Get Article</Button> */}
        </Provider>
      </ThemeProvider>
    </BrowserRouter>
  );
};

export default App;
