import React from 'react'
import {Typography, Box, AppBar, Toolbar, IconButton, Button, Tooltip, Avatar, Menu, MenuItem} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import Authdialog from './AuthDialog';
import {logout_user} from '../actions/authActions';
import store from '../store';
import WalletDialog from './WalletDialog';
import {Link} from 'react-router-dom';


const Header = ({authSuccess, authorized, walletAddress, setWalletAddress, handleDrawerToggle}) => {
  const [authDialog, setAuthDialog] = React.useState(false);
  const [anchorElUser, setAnchorElUser] = React.useState(null);
  const [walletOpen, setWalletOpen] = React.useState(false);

  const openDialog = () => {
    setAuthDialog(true);
  }
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };
  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const logoutClick = () => {
    setAnchorElUser(null);
    store.dispatch(logout_user());
    authSuccess(false);
  }
  const checkClick = () => {
    setAnchorElUser(null);
      console.log('Profile Click.')
  }
  const connect_wallet = () => {
      setAnchorElUser(null);
      setWalletOpen(true);
  }

  const onWalletClose = () => {
      setWalletOpen(false);
  }
  return (
    <Box sx={{ flexGrow: 1 }}>
        <AppBar position="relative" elevation={1} sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
            <Toolbar>
                <IconButton
                    size="large"
                    edge="start"
                    color="inherit"
                    aria-label="menu"
                    onClick={handleDrawerToggle}
                    sx={{ mr: 2, display: authorized ? {sm: 'none'} : "none" }}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    <Link to="/" className='link'>
                        NFTs
                    </Link>
                </Typography>
                {
                    authorized ? 
                    <Box sx={{ flexGrow: 0 }}>
                        <Tooltip title="Open settings">
                            <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                                <Avatar alt={store.getState().auth.user.username.toUpperCase()}/>
                            </IconButton>
                        </Tooltip>
                        <Menu
                            sx={{ mt: '45px' }}
                            id="menu-appbar"
                            anchorEl={anchorElUser}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={Boolean(anchorElUser)}
                            onClose={handleCloseUserMenu}
                        >
                            
                            {
                                walletAddress !== "" ? 
                                <MenuItem divider={true} dense={true} disableRipple={true}>
                                    <Typography textAlign="center">{walletAddress}</Typography>
                                </MenuItem> : 
                                <MenuItem onClick={connect_wallet} divider={true} dense={true} disableRipple={true}>
                                    <Typography textAlign="center">Connect Wallet</Typography>
                                </MenuItem>
                            }
                            <MenuItem onClick={checkClick}>
                                <Typography textAlign="center">Profile</Typography>
                            </MenuItem>
                            <MenuItem onClick={logoutClick}>
                                <Typography textAlign="center">Logout</Typography>
                            </MenuItem>
                        </Menu>
                    </Box>
                    :
                    <Button onClick={openDialog} color="inherit">Authorize</Button>
                }
            </Toolbar>
        </AppBar>
        <Authdialog open={authDialog} setOpen={setAuthDialog} authSuccess={authSuccess}/>
        <WalletDialog open={walletOpen} onClose={onWalletClose} setWalletAddress={setWalletAddress} />
    </Box>
  )
}

export default Header;
