import React from "react";
import {Dialog, DialogTitle, List, ListItem, ListItemAvatar, Avatar, ListItemText, Snackbar } from '@mui/material';
import store from  '../store';
import {set_address} from '../actions/walletAction';

const WalletDialog = ({ open, onClose, setWalletAddress }) => {
    const [wallets, setWallets] = React.useState(store.getState().wallet.list);
    const [openAlert, setOpenAlert] = React.useState(false);
    const [alertMessage, setAlertMessage] = React.useState("");
    const [install, setInstall] = React.useState('');

    const connect_wallet = () => {
      window.ethereum.request({method: 'eth_requestAccounts'}).then(function(accounts) {
        if(accounts.length > 0) {
          if(window.ethereum.networkVersion === '4') {
            window.ethereum.request({method: 'eth_getBalance', params: [accounts[0], 'latest']})
            .then(function(balance) {
              store.dispatch(set_address({address: accounts[0], balance: balance})).then(function() {
                setWallets(store.getState().wallet.list);
                var address = accounts[0];
                var formateWalletAddress = address.substring(0, 5) + '...' + address.substring(address.length - 4, address.length);
                setWalletAddress(formateWalletAddress);
                onClose();
              });
            })
          } else {
            setAlertMessage("Test network is not allow, use real network.");
            setInstall('');
            setOpenAlert(true);
            onClose();
          }
        }
      }).catch(function(error) {
        if(error.code === 4001) {
          setAlertMessage("Please connect your Metamask Wallet.");
          setInstall('');
          setOpenAlert(true);
          onClose();
        }
      });
    }


    const handleListItemClick = async (value) => {
      if(value === 'metamask') {
        if(typeof window.ethereum !== 'undefined' && window.ethereum.isMetaMask) {
          if(window.ethereum.networkVersion !== '4') {
            window.ethereum.request({method: 'wallet_switchEthereumChain',params: [{ chainId: '0x4' }]}).then(function() {
              connect_wallet();
            }).catch(function(error) {
              console.log("here");
              if(error.code === 4001) {
                setAlertMessage(error.message);
                setInstall('');
                setOpenAlert(true);
                onClose();
              }
            })
          } else {
            connect_wallet();
          }
        } else {
          setAlertMessage("Please install Metamask extension.");
          setInstall('https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn?hl=en');
          setOpenAlert(true);
          onClose();
        }
      }
    }
    const closeAlert = () => {
      setOpenAlert(false);
    }

    const alertAction = (
      install !== "" ?
      <a target="_blank" rel="noreferrer" className="alert_action" href={install}>Install</a>
      : ""
    );
    return(
      <>
        <Dialog onClose={onClose} open={open}>
          <DialogTitle>Transaction Wallet</DialogTitle>
          <List sx={{ pt: 0 }}>
            {wallets.map((wallet) => (
              <ListItem button onClick={() => handleListItemClick(wallet.link)} key={wallet.link}>
                <ListItemAvatar>
                <Avatar sx={{ bgcolor: 'transparent', color: 'transparent' }}>
                    <img className="wallet_icon" src={wallet.icon} alt={wallet.title} />
                </Avatar>
                </ListItemAvatar>
                <ListItemText primary={wallet.title} />
              </ListItem>
            ))}
          </List>
        </Dialog>
        <Snackbar
          anchorOrigin={{vertical: 'bottom', horizontal: 'center'}}
          open={openAlert}
          onClose={closeAlert}
          message={alertMessage}
          key={{vertical: 'center', horizontal: 'bottom'}}
          action={alertAction}
        />
      </>
    );
}

export default WalletDialog