import axios from 'axios';
import {HOST,LOAD_NFTS, LOAD_NFTS_FAIL, UPDATE_NFT_SUCCESS, UPDATE_NFT_FAIL} from './types';
import store from '../store'

export const uploadNft = (input) => dispatch => {
    return new Promise((resolve, reject) => {
        axios({
            method: 'post',
            url: `${HOST}/nfts/`,
            data: input,
            headers: {
                "Content-Type": "multipart/form-data",
                "Authorization": localStorage.getItem('_token')
            }
        }).then(function(response) {
            if(response.status === 201 || response.status === 200) {
                dispatch(loadNfts())
                resolve(response);
            } else {
                console.error(response);
                resolve(response);
            }
        }).catch(function(error) {
            reject(error.response.data.errors);
        })
    })
}

export const loadNfts = () => dispatch => {
    return new Promise((resolve, reject) => {
        if(localStorage.getItem('_token')) {
            axios({
                method: 'get',
                url: `${HOST}/nfts/all`,
                headers: {
                    "Content-Type":"application/json",
                    "Authorization":localStorage.getItem('_token')
                }
            }).then(function(response) {
                if(response.status === 200 && response.data.success === true) {
                    dispatch({
                        type:LOAD_NFTS,
                        payload: response.data
                    })
                }
                resolve(response);
            }).catch(function(err) {
                dispatch({
                    type: LOAD_NFTS_FAIL
                })
                reject(err);
            })
        }
    })
}

export const updateNfts = (input) => dispatch => {
    return new Promise((resolve, reject) => {
        axios({
            method: 'put',
            url: `${HOST}/nfts`,
            headers: {
                "Content-Type": "multipart/form-data",
                "Authorization": localStorage.getItem('_token')
            },
            data: input
        }).then(function(response) {
            if(response.status === 201 && response.data.success) {
                dispatch(loadNfts()).then(function(nfts) {
                    dispatch({
                        type: UPDATE_NFT_SUCCESS,
                        payload: nfts.data
                    })
                    resolve({nft:response.data.data.nft, nfts:nfts.data.data});
                }).catch(function(error) {
                    reject(error.response);
                });
            } else {
                console.log(response)
                resolve(response);
            }
        }).catch(function(error) {
            if(error.response.status === 403) {
                dispatch({
                    type: UPDATE_NFT_FAIL,
                    payload: error.response.data.errors
                })
            } else {
                throw error;
            }
            reject(error.response);
            
        })
    })
}

export const deleteMultiple = (deleteIds) => dispatch => {
    return new Promise(function(resolve, reject) {
        axios({
            url: `${HOST}/nfts`,
            headers: {
                "Content-Type": "application/json",
                "Authorization": localStorage.getItem('_token')
            },
            method: 'delete',
            data: JSON.stringify({ids: deleteIds})
        }).then(function(response) {
            dispatch(loadNfts()).then(function(nfts) {
                dispatch({
                    type: UPDATE_NFT_SUCCESS,
                    payload: nfts.data
                })
                resolve({nfts:nfts.data.data});
            }).catch(function(error) {
                reject(error.response);
            });
        }).catch(function(error) {
            reject(error.response);
        });
    });
}