import { useRoutes } from "react-router-dom";
import React from "react";
import Home from "./components/Home";
import ListNFT from './components/ListNFT';
import CreateNFT from "./components/CreateNFT";
import Updatenft from './components/UpdateNft';

export default function Router({authorized}) {
  return useRoutes([
    {
      path: "/",
      element: <Home />,
    },
    {
      path: "/create-nft",
      element: <CreateNFT authorized={authorized}/>
    },
    {
      path: "/list-nft",
      element: <ListNFT authorized={authorized}/>
    },
    {
      path: "/update-nft",
      element: <Updatenft authorized={authorized}/>
    }
  ]);
}
