import React from "react";
import {
  Box,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Drawer,
  Toolbar,
  Paper
} from "@mui/material";
import NoteAddIcon from '@mui/icons-material/NoteAdd';
import ListAltIcon from '@mui/icons-material/ListAlt';
import {Link} from 'react-router-dom';
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles(theme => ({
  link: {
    marginBottom: "3px",
    marginLeft: "3px",
    marginRight: "3px",
    border: '1px solid #FAD9E6'
  }
}))

const drawerWidth = 240;

const Sidebar = ({mobileOpen, handleDrawerToggle, authorized}) => {
  const classes = useStyles();

  const drawer = (
    <div>
      <Toolbar />
      <Divider />
      <List>
        <Link to="/create-nft" className="link">
          <Paper elevation={1} className={classes.link}>
            <ListItem>
              <ListItemIcon>
                  <NoteAddIcon />
              </ListItemIcon>
              <ListItemText primary="Create" className="text-black"/>
            </ListItem>
          </Paper>
        </Link>
        <Link to="/list-nft" className="link">
          <Paper elevation={1} className={classes.link}>
            <ListItem>
              <ListItemIcon>
                  <ListAltIcon />
              </ListItemIcon>
              <ListItemText primary="MY NFTs" className="text-black"/>
            </ListItem>
          </Paper>
        </Link>
      </List>
    </div>
  );

  const container = window !== undefined ? () => window.document.body : undefined;

  return (
    <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
        display={authorized !== true ? "none" : "block"}
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
  );
};

export default Sidebar;
