
import {Dialog, Box,AppBar,Toolbar,Typography, IconButton, Tabs, Tab, useTheme, useMediaQuery} from '@mui/material'
import CloseIcon from '@mui/icons-material/Close';
import React, {useState} from 'react';
import Signin from './Signin';
import Signup from './Signup';
import PropTypes from 'prop-types';
import {makeStyles} from '@mui/styles'

const useStyles = makeStyles((theme) => ({
    dialog: {
        height: '550px'
    }
}))

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            {children}
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

const Authdialog = ({open, setOpen, authSuccess}) => {
    
    const theme = useTheme();
    const classes = useStyles();
    const [value, setValue] = useState(0);
    const handleClose = () => {
        setOpen(false);
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Dialog open={open} onClose={handleClose} fullWidth={true} maxWidth='sm' fullScreen={useMediaQuery(theme.breakpoints.down('sm'))}>
            <Box className={classes.dialog} component="div">
            <AppBar sx={{ position: 'static' }} elevation={0}>
                <Toolbar>
                    {useMediaQuery(theme.breakpoints.down('sm')) ? <IconButton
                      edge="start"
                      color="inherit"
                      onClick={handleClose}
                      aria-label="close"
                      variant="fullWidth"
                    >
                      <CloseIcon />
                    </IconButton> : ''}
                    <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                      Authentication
                    </Typography>
                </Toolbar>
            </AppBar>
                <Tabs
                    variant='fullWidth'
                    value={value}
                    onChange={handleChange}
                    aria-label='Authetication tabs'
                >
                    <Tab label="SignUp" {...a11yProps(0)}/>
                    <Tab label="SignIn" {...a11yProps(1)}/>
                </Tabs>
                <TabPanel value={value} index={0}>
                    <Signup handleClose={handleClose} authSuccess={authSuccess} />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <Signin handleClose={handleClose} authSuccess={authSuccess} />
                </TabPanel>
            </Box>
        </Dialog>
    );
}

export default Authdialog;
