import {
    SIGNUP_INPUT, 
    SIGNIN_INPUT, 
    SIGNUP_ERROR, 
    SIGNUP_SUCCESS, 
    SIGNIN_SUCCESS, 
    AUTH_SUCCESS, 
    AUTH_FAIL, 
    SIGNIN_ERROR,
    LOGOUT_USER,
    LOAD_NFTS_FAIL,
    HOST
} from './types';
import axios from 'axios';
import { loadNfts } from './nftActions';

export const signup_input_change = (input) => dispatch => {
    dispatch({
        type: SIGNUP_INPUT,
        payload: input
    })
}

export const signin_input_change = (input) => dispatch => {
    dispatch({
        type: SIGNIN_INPUT,
        payload: input
    })
}

export const signup_user = (input) => dispatch => {
    return new Promise(function(resolve, reject) {
        get_ip().then(function(data) {
            axios({
                method:'post',
                url: `${HOST}/user`,
                headers: {
                    "Content-type": "application/json"
                },
                data: JSON.stringify({
                    username: input.username,
                    email: input.email,
                    password: input.password,
                    ip_address: data.ip ? data.ip : ''
                })
            }).then(function(response) {
                if(response.status === 201 && response.data.success === true) {
                    localStorage.setItem('_token', response.data.token);
                    localStorage.setItem('_track', response.data.track);
                    dispatch({
                        type: SIGNUP_SUCCESS,
                        payload: {
                            username: response.data.user.username,
                            email: response.data.user.email,
                            token: response.data.token,
                            role: response.data.user.role.slug,
                            profile_pic: response.data.user.profile_pic,
                            track: response.data.track
                        }
                    })
                }
                resolve();
            }).catch(function(error) {
                if(error.response.status === 403) {
                    console.log(error.response);
                    dispatch({
                        type: SIGNUP_ERROR,
                        payload: error.response.data.errors
                    })
                }
                resolve();
            })
        }).catch(function(err) {
            console.log(err.message);
        })
    })
}

export const get_ip = () => {
    return new Promise((resolve, reject) => {
        axios.get('https://api.ipify.org?format=json').then(function(response) {
            console.log(response.data)
            return resolve(response.data);
        }).catch(function(err) {
            return reject(err);
        })
    })
}

export const signin_user = (input) => dispatch => {
    return new Promise((resolve, reject) => {
        get_ip().then(function(data) {
            axios({
                method: 'get',
                url: `${HOST}/user`,
                params: {
                    username: input.username ? input.username : '',
                    password: input.password ? input.password : '',
                    ip_address: data.ip ? data.ip : ''
                },
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(response => {
                if(response.status === 200 && response.data.success === true) {
                    localStorage.setItem('_token', response.data.token);
                    localStorage.setItem('_track', response.data.track);
                    dispatch({
                        type: SIGNIN_SUCCESS,
                        payload: {
                            username: response.data.user.username,
                            email: response.data.user.email,
                            token: response.data.user.token,
                            role: response.data.user.role.slug,
                            profile_pic: response.data.user.profile_pic,
                            track: response.data.track
                        }
                    })
                    dispatch(loadNfts());
                }
                resolve();
            }).catch(error => {
                if(error.response.status === 403) {
                    console.log(error.response.data);
                    dispatch({
                        type:SIGNIN_ERROR,
                        payload: error.response.data.errors
                    })
                }
                else {
                    console.log(error)
                }
                resolve();
            })
        }).catch(function(err) {
            console.log(err.message)
        })
    })
}

export const verify_token = () => dispatch => {
    var token = localStorage.getItem('_token')
    return new Promise(function(resolve, reject) {
        axios.get(`${HOST}/`, {
            headers: {
                "Content-Type": "application/json",
                "Authorization": token
            }
        }).then(function(response) {
            console.log(response)
            if(response.status === 200) {
                dispatch({
                    type: AUTH_SUCCESS,
                    payload: {
                        username: response.data.user.username,
                        email: response.data.user.email,
                        token: token,
                        role: response.data.user.role.slug,
                        profile_pic: response.data.user.profile_pic,
                        track: localStorage.getItem('_track') !== null ? localStorage.getItem('_track') : ""
                    }
                })
                resolve();
            }
        }).catch(function(err) {
            console.log(err)
            if(err.response.status === 401) {
                localStorage.removeItem('_token');
                dispatch({
                    type:AUTH_FAIL
                })
            }
            resolve();
        })
    })
}

export const logout_user = () => dispatch => {
    if(localStorage.getItem('_token') !== null) {
        localStorage.removeItem('_token');
        localStorage.removeItem('_track');
    }
    dispatch({
        type: LOGOUT_USER
    })
    dispatch({
        type: LOAD_NFTS_FAIL
    })
}