import {
    VERIFY_AUTH, 
    SIGNUP_INPUT, 
    SIGNIN_INPUT,
    SIGNUP_ERROR,
    SIGNIN_ERROR, 
    SIGNUP_SUCCESS, 
    AUTH_SUCCESS, 
    AUTH_FAIL,
    SIGNIN_SUCCESS,
    LOGOUT_USER
} from '../actions/types'

const initialState = {
    user: {
        username: '',
        email: '',
        token: '',
        profile_pic: '',
        role: '',
        track: ''
    },
    signup: {
        username:'',
        email: '',
        password: ''
    },
    signin: {
        username: '',
        password: ''
    },
    signup_errors: {
        username: [],
        email: [],
        password: [],
        general: []
    },
    signin_errors: {
        username: [],
        password: [],
        general: []
    }
}

export default function auth(state = initialState, action) {
    switch(action.type) {
        case VERIFY_AUTH: 
            return {
                ...state
            }
        case SIGNUP_INPUT:
            return {
                ...state,
                'signup': {
                    ...(state.signup),
                    [action.payload.name]:action.payload.value
                }
            }
        case SIGNIN_INPUT:
            return {
                ...state,
                'signin': {
                    ...(state.signin),
                    [action.payload.name]:action.payload.value
                }
            }
        case SIGNUP_ERROR:
            return {
                ...state,
                'signup_errors': {
                    ...(action.payload)
                }
            }
        case SIGNIN_ERROR:
            return {
                ...state,
                'signin_errors': {
                    ...(action.payload)
                }
            }
        case SIGNUP_SUCCESS:
        case SIGNIN_SUCCESS:
        case AUTH_SUCCESS:
            return {
                ...state,
                'signup': {
                    username:'',
                    email: '',
                    password: ''
                },
                'signin': {
                    username: '',
                    password: ''
                },
                'signup_errors':{
                    username: [],
                    email: [],
                    password: [],
                    general: []
                },
                'signin_errors':{
                    username: [],
                    password: [],
                    general: []
                },
                user: {
                    ...(action.payload)
                }
            }
        case AUTH_FAIL:
        case LOGOUT_USER:
            return {
                ...state,
                user: {
                    username: '',
                    email: '',
                    token: '',
                    role: '',
                    profile_pic: '',
                    track: ''
                }
            }
        default:
            return state;
    }
}